@import url('https://fonts.googleapis.com/css?family=Vibur:400');

.App {
  text-align: center;
}


.App-header {
 background-color: #532e60 ;
  /* background-color: #262626; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 10vmin);
  color: white;
}

.react-activity-calendar {
  width: 100vh;
}

.thetile {
  font-size: 0.5em;
  margin: 1em;
}

.styles-module_container__GBNxC {
  max-width: 95% !important;
}


.neonText {
  animation: flicker 1.5s infinite alternate;
  color: #fff;
}

h1[class="neonText"] {
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.5rem;
  margin: 0;   
}

h1 {
  font-size: 2rem;
  margin: 0;   
}
/* Flickering animation */
@keyframes flicker {
    
  0%, 18%, 22%, 25%, 53%, 57%, 100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #ffeb3b,
      0 0 80px #ffeb3b,
      0 0 90px #ffeb3b,
      0 0 100px #ffeb3b,
      0 0 150px #ffeb3b;
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}


/* Additional styling */
  
h2 {
 font-size: 1.8rem;
 font-family: "Vibur", sans-serif;
 margin: 0;
}
.container {
  margin-top: 20vh;
}



h1, h2 {
  text-align: center;
  font-weight: 300;
}

.zhaopai {
  /* animation: flicker 1.5s infinite alternate;   */
  border: 0.2rem solid #fff;
  border-radius: 2rem;
  padding: 0.4em;
  margin: 0.4em;
  position: absolute;
  top: 5%;
  box-shadow: 0 0 .2rem #fff,
            0 0 .2rem #fff,
            0 0 2rem #ffeb3b,
            0 0 0.8rem #ffeb3b,
            0 0 2.8rem #ffeb3b,
            inset 0 0 1.3rem #ffeb3b; 
}

@keyframes pulsate {
    
  100% {

      text-shadow:
      0 0 4px #fff,
      0 0 11px #fff,
      0 0 19px #fff,
      0 0 40px #ffeb3b,
      0 0 80px #ffeb3b,
      0 0 90px #ffeb3b,
      0 0 100px #ffeb3b,
      0 0 150px #ffeb3b;
  
  }
  
  0% {

    text-shadow:
    0 0 4px #fff,
    0 0 10px #fff,
    0 0 18px #fff,
    0 0 38px #ffeb3b,
    0 0 73px #ffeb3b,
    0 0 80px #ffeb3b,
    0 0 94px #ffeb3b,
    0 0 140px #ffeb3b;

}
}